

import * as React from 'react';
import { StyleSheet, Image, View, Text, TouchableOpacity, Platform } from 'react-native';
import { themePattern } from '../../constants/pattern';
import BtnCircle from '../buttons/BtnCircle';
import { useNavigation } from '@react-navigation/native';
import { FontAwesome } from '@expo/vector-icons';
import BtnRectangle from '../buttons/BtnRectangle';
import BtnSquare from '../buttons/BtnSquare';

const rota: any = 'ModalNav'

const rotaInstructions: any = 'Instructions'
const rotaCredits: any = 'Credits'
const rotaShared: any = 'Shared'





export default function NavBarContainer() {


  const navigation = useNavigation();
  let logo = require('./../../assets/logo.png');


  // Função Referencia
  let openHelp = () => {
    navigation.navigate(rotaInstructions) // open drawer
  };
  let openCredits = () => {
    navigation.navigate(rotaCredits) // open drawer

  };
  let openShared = () => {
    navigation.navigate(rotaShared) // open drawer
  };

  let onPress = () => {
    navigation.navigate(rota) // open drawer
  };



  function device() {

    if (Platform.OS === 'web')
      return (<><View style={styles.Opcoes}>

        <View style={styles.item}>
          <BtnSquare
            icon={"question"}
            key={"ajuda"}
            name={"SOBRE A MÍDIA"}
            onPress={() => openHelp()} />
        </View>
        <View style={styles.item}>
          <BtnSquare
            icon={"group"}
            key={"creditos"}
            name={"CRÉDITO"}
            onPress={() => openCredits()} />
        </View>
        <View style={styles.item}>
          <BtnSquare
            icon={"share-alt"}
            key={"compartilhar"}
            name={"COMPARTILHAR"}
            onPress={() => openShared()} />
        </View>
      </View></>)

    else
      return (<><TouchableOpacity
        onPress={() => onPress()}
      >
        <View style={styles.iconContainer}>
          <FontAwesome
            name="ellipsis-v"
            style={styles.icon} />
        </View>
      </TouchableOpacity></>)
  }

  return (
    <View style={styles.bar} >
      <View style={styles.logo}>

        <Image source={logo} style={styles.logoImage} resizeMode='contain' />

      </View>

      <View style={styles.iconDivisor}>
        {device()}
      </View>

    </View>
  );
}


const styles = StyleSheet.create({
  bar: {
    height: Platform.OS === 'web' ? 100 : 80,
    marginLeft: 15,
    marginRight: 15,
    backgroundColor: themePattern.mobile.white,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between'

  },
  logo: {
    flex: 1,
    justifyContent: 'flex-start',
    height: 80
  },
  iconDivisor: {
    flexGrow: 1,
    alignItems: "flex-end"
  },

  Opcoes: {
    flexDirection: 'row'
  },

  item: {
    flex: 1,
    width:100

  },




  iconContainer: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    width: 56,
    height: 56
  },
  icon: {
    color: themePattern.mobile.gray,
    fontSize: 30
  },

  logoImage: {
    width: 'auto',
    height: '100%'
  }
});
