import * as React from 'react';
import { View, Text, StyleSheet,Dimensions } from 'react-native';
import { PropsDetails } from '../../types';
import  {themePattern}  from '../../constants/pattern';

import { aminoacidos } from '../../assets/aminoacidos';


const { width, height } = Dimensions.get('window')


export default function Details(props : PropsDetails ) {

    const { name, info } = aminoacidos(props.aminoAcid)
    return (
      <View style={styles.container}>
          <View style={styles.text}>
            
          <Text style={styles.descrebePrimary}>{info[0]}</Text>
          <Text style={styles.descrebeSecundary}>{info[1]}</Text>
        </View>
        <View style={styles.btn}>{props.btn}</View>
      </View>
    );
  }
  
  const styles = StyleSheet.create({
    container: {
      flex: 1,
      justifyContent: 'space-between',
      padding: 20,
      backgroundColor:themePattern.tablet.white,
      borderRadius: 10,
      borderStyle: 'solid',
        borderColor:'black',
        borderWidth:2,
        height:height*0.6
    },
    descrebePrimary:{
        backgroundColor:themePattern.tablet.color1,
        color:themePattern.tablet.white,
        fontSize:themePattern.tablet.par2,
        padding: 10,
        borderStyle: 'solid',
        borderColor:'black',
        borderWidth:2,
        borderRadius: 10

    }, 
    descrebeSecundary:{
        color: themePattern.tablet.black,
        fontSize:themePattern.tablet.par2,
        marginTop: themePattern.tablet.retreat
        
    },
    btn:{
        height:'min-height',
        position:'relative',
        bottom: '-70px'
    },
    text:{
        flexGrow:1,
        flex:1,
        justifyContent:'flex-start'
    }
  });
